<template>
    <div>
        <div class="container">
            <div class="center">
                <div class="status-mark" v-if="currentIndex>=0&&pictures[currentIndex].status!=0">
                    <img src="./assets/tgbq.png" v-if="pictures[currentIndex].status>0"/>
                    <img src="./assets/btgbq.png" v-else/>
                </div>
                <div class="picture">
                    <img class="picture-cropper" src="" alt=" "/>
                    <!--btn-prev1 btn-next1 -->
                    <img class="picture-prev button" src="./assets/btn-prev2.png" v-on:click="prev"/>
                    <img class="picture-next button" src="./assets/btn-next2.png" v-on:click="next"/>
                    <div class="picture-toggle">
                        <img class="picture-rotate_ac button" src="data:image/png;base64,"
                             v-on:click="rotateAC"/>
                        <img class="picture-rotate_c button" src="data:image/png;base64,"
                             v-on:click="rotateC"/>
                        <img class="picture-adapt button" src="data:image/png;base64,"
                             v-on:click="adapt"/>
                    </div>
                </div>
            </div>

            <div class="bottom">
                <swiper class="thumbs-swiper" :options="thumbsSwiperOption">
                    <swiper-slide :key="index" v-for="(picture,index) in pictures">
                        <div v-on:click="to(index);">
                            <div class="thumbs-swiper-picture" :class="(index==currentIndex?'active':'')+' '+(picture.status==1?'tg':(picture.status==-1?'btg':''))"
                                 :style="'background-image: url('+picture.small+');'"></div>
                        </div>
                    </swiper-slide>
                </swiper>
                <img class="thumbs-swiper-prev button" src="./assets/btn-prev2.png"
                     v-on:click="thumbsSwiper.slidePrev()"/>
                <img class="thumbs-swiper-next button" src="./assets/btn-next2.png"
                     v-on:click="thumbsSwiper.slideNext()"/>
            </div>
        </div>
    </div>
</template>

<script>
    import Cropper from 'cropperjs';
    import "@/../node_modules/cropperjs/src/index.css"
    // import Swiper from 'swiper'
    // import 'swiper/dist/css/swiper.min.css'
    export default {
        name: "PicBrowser",
        data() {
            return {
                cropper:null,
                currentIndex: -1,
                pictures: [],
                callback: {},
                gallerySwiperOption: {
                    direction: 'horizontal',
                    loop: false,
                    lazy: true,
                    navigation: {
                        prevEl: '.gallery-swiper-prev',
                        nextEl: '.gallery-swiper-next'
                    }
                },
                thumbsSwiperOption: {
                    direction: 'horizontal',
                    loop: false,
                    slidesPerView: 'auto'
                }
            }
        },
        methods: {
            initPicBrowser(data) {
                if(data.callback){
                    this.callback.onPicChange=data.callback.onPicChange;
                    this.callback.onCrossEnd=data.callback.onCrossEnd;
                    this.callback.onCrossStart=data.callback.onCrossStart;
                }
                if(data.pictures.length==0){
                    this.currentIndex = -1;
                }else{
                    this.currentIndex = 0;
                }
                this.pictures = data.pictures;
                for(var i in this.pictures){
                    if(!this.pictures[i].rotate){
                        this.pictures[i].rotate=0;
                    }
                }

                var pic="";
                if(data.pictures.length>0){
                    pic=data.pictures[0].big;
                }else{
                    pic="/qishui/base/static/res/img/blank.png"
                }
                var image = this.$el.getElementsByClassName("picture-cropper")[0];
                image.src=pic;
                var self=this;
                this.cropper = new Cropper(image, {
                    dragMode:"move",
                    autoCrop: false,
                    ready(event) {
                        if(self.currentIndex>0){
                            this.cropper.rotateTo(self.pictures[self.currentIndex].rotate);
                        }
                    }
                });

                if(typeof this.callback.onPicChange=="function"){
                    if(this.currentIndex>=0){
                        this.callback.onPicChange(this.currentIndex,this.pictures[this.currentIndex]);
                    }else{
                        this.callback.onPicChange(this.currentIndex,null);
                    }
                }
            },
            updatePictures(pictures){
                if(pictures.length==0){
                    this.currentIndex = -1;
                }else{
                    this.currentIndex = 0;
                }
                this.pictures = pictures;
                for(var i in this.pictures){
                    if(!this.pictures[i].rotate){
                        this.pictures[i].rotate=0;
                    }
                }

                var pic="";
                if(pictures.length>0){
                    pic=pictures[0].big;
                }else{
                    pic="/qishui/base/static/res/img/blank.png"
                }
                this.cropper.replace(pic);

                if(typeof this.callback.onPicChange=="function"){
                    if(this.currentIndex>=0){
                        this.callback.onPicChange(this.currentIndex,this.pictures[this.currentIndex]);
                    }else{
                        this.callback.onPicChange(this.currentIndex,null);
                    }
                }
            },
            getCurrentIndex() {
                return this.currentIndex;
            },
            getCurrentPicture() {
                if(this.currentIndex==-1){
                    return null;
                }
                return this.pictures[this.currentIndex];
            },
            next() {
                if (this.currentIndex + 1 < this.pictures.length) {
                    this.currentIndex++;
                    // this.thumbsSwiper.slideTo(this.currentIndex-2);
                    var pic=this.pictures[this.currentIndex];
                    this.cropper.replace(pic.big);
                    if(typeof this.callback.onPicChange=="function"){
                        this.callback.onPicChange(this.currentIndex,pic);
                    }
                }else if(this.currentIndex + 1 == this.pictures.length){
                    if(typeof this.callback.onCrossEnd=="function"){
                        this.callback.onCrossEnd();
                    }
                }
            },
            prev() {
                if (this.currentIndex - 1 >= 0) {
                    this.currentIndex--;
                    // this.thumbsSwiper.slideTo(this.currentIndex-2);
                    var pic=this.pictures[this.currentIndex];
                    this.cropper.replace(pic.big);
                    if(typeof this.callback.onPicChange=="function"){
                        this.callback.onPicChange(this.currentIndex,pic);
                    }
                }else if(this.currentIndex - 1 == -1){
                    if(typeof this.callback.onCrossStart=="function"){
                        this.callback.onCrossStart();
                    }
                }
            },
            to(index){
                if (index >= 0 && index < this.pictures.length){
                    this.currentIndex=index;
                    // this.thumbsSwiper.slideTo(this.currentIndex-2);
                    var pic=this.pictures[this.currentIndex];
                    this.cropper.replace(pic.big);
                    if(typeof this.callback.onPicChange=="function"){
                        this.callback.onPicChange(this.currentIndex,pic);
                    }
                }
            },
            rotateC(){
                if(this.currentIndex>=0){
                    var pic=this.pictures[this.currentIndex];
                    pic.rotate=(pic.rotate+360+90)%360;
                    this.cropper.rotateTo(pic.rotate);
                }
            },
            rotateAC(){
                if(this.currentIndex>=0){
                    var pic=this.pictures[this.currentIndex];
                    pic.rotate=(pic.rotate+360-90)%360;
                    this.cropper.rotateTo(pic.rotate);
                }
            },
            adapt(){
                if(this.currentIndex>=0){
                    this.cropper.replace("");
                    this.cropper.replace(this.pictures[this.currentIndex].big);
                }
            },
        },
        mounted() {
            const self = this;
            this.thumbsSwiperOption.on = {
                init() {
                    self.thumbsSwiper = this;
                }
            }
        }
    }
</script>

<style scoped>
    .button {
        -moz-user-select: none;
        -o-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        cursor: pointer;
    }

    .container {
        position: relative;
        width: 100%;
        height: 600px;
        overflow: hidden;
    }

    /*--------------------------------*/

    .center {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        padding-bottom: 59.5px;
    }

    .status-mark{
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: absolute;
        left: 0px;
        top: 0px;
        z-index:1;
        width: 100%;
        height: 100%;
        padding-bottom: 59.5px;
        overflow: hidden;
    }

    .status-mark > img{
        display: block;
        width: 30%;
        height: auto;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10%;
    }

    .picture {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: #f7f7f7;
    }

    .picture-cropper {
        display: block;
        max-width: 100%;
        max-height: 100%;
        margin: 0px auto;
    }

    .picture-prev {
        position: absolute;
        left: 14px;
        top: 50%;
        z-index: 1;
        width: 19.6px;
        height: 32.2px;
        margin: -16.1px 0px;
    }

    .picture-next {
        position: absolute;
        right: 14px;
        top: 50%;
        z-index: 1;
        width: 19.6px;
        height: 32.2px;
        margin: -16.1px 0px;
    }

    .picture-toggle {
        position: absolute;
        left: 50%;
        bottom: 21px;
        z-index: 1;
        width: 140px;
        height: 28px;
        margin:0px -70px;
        border-radius: 3.5px;
        background-color: rgba(0,0,0,0.5);
        text-align: center;
    }

    .picture-rotate_ac{
        width: 18.9px;
        height: 17.5px;
        margin-top: 4.9px;
    }

    .picture-rotate_c{
        width: 18.9px;
        height: 17.5px;
        margin-top: 4.9px;
        margin-left: 14px;
    }

    .picture-adapt{
        width: 17.5px;
        height: 17.5px;
        margin-top: 4.9px;
        margin-left: 35px;
    }

    >>> .cropper-bg{
        background-image: none;
        background-color: #f7f7f7;
    }

    /*--------------------------------*/

    .bottom {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: absolute;
        left: 0px;
        bottom: 0px;
        z-index:2;
        width: 100%;
        height: 59.5px;
        padding: 0px 35px;
        background-color: white;
    }

    .thumbs-swiper {
        display: inline-block;
        width: 100%;
        height: 100%;
    }

    .thumbs-swiper-prev {
        position: absolute;
        left: 14px;
        top: 50%;
        z-index: 10;
        width: 9.8px;
        height: 16.1px;
        margin: -8.05px 0px;
    }

    .thumbs-swiper-next {
        position: absolute;
        right: 14px;
        top: 50%;
        z-index: 10;
        width: 9.8px;
        height: 16.1px;
        margin: -8.05px 0px;
    }

    .thumbs-swiper >>> .swiper-slide {
        position: relative;
        width: 59.5px;
        height: 100%;
        margin: 0px auto;
    }

    .thumbs-swiper-picture {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: absolute;
        left: 50%;
        top: 50%;
        width: 45.5px;
        height: 45.5px;
        margin: -22.75px -22.75px;
        border: 1px solid darkgray;
        background-position: center center;
        background-size: contain;
        background-repeat: no-repeat;
        cursor: pointer;
    }

    .thumbs-swiper-picture.active {
        box-shadow: 0px 0px 8px black;
    }

    .thumbs-swiper-picture.tg.active {
        box-shadow: 0px 0px 8px limegreen;
    }

    .thumbs-swiper-picture.btg.active {
        box-shadow: 0px 0px 8px red;
    }

    .thumbs-swiper-picture.tg {
        border-color: limegreen;
    }

    .thumbs-swiper-picture.btg {
        border-color: red;
    }
</style>